<template>
  <div class="my-cover">
    <div class="operateBox"
         v-for="(item,index) in picnum"
         :key="item"
         @click="openDialog(index)">
      <span v-show="spanShow[index]"></span>
      <img class="img" :src="images[index]" alt="">
    </div>
    <!--  -->
    <el-dialog
      :append-to-body="true"
      :visible.sync="dialogVisible"
      width="720px">
      <el-card class="box-card">
        <div style="padding-bottom: 20px;">
          <el-radio-group v-model="collect" size="mini" @change="collectChange">
            <el-radio-button :label="false"></el-radio-button>
            <el-radio-button :label="true"></el-radio-button>
          </el-radio-group>
        </div>
        <!--  -->
        <el-row :gutter="10">
          <el-col class="img_item" :xs="12" :sm="6" :md="6" :lg="4"
                  v-for="(item,index) in imageList"
                  :key="item.id"
                  @click.native="isShow(item)">
            <div class="img_box">
              <img :src="item.url" alt="">
              <div class="selected" v-show="item.isShow">
                <img src="./img/selected.png" alt="">
              </div>
            </div>
          </el-col>
        </el-row>
        <!-- / -->
        <el-pagination
          background
          layout="sizes, prev, pager, next"
          :total="total_count"
          class="pageCount"
          :page-sizes="[6, 12, 24, 30]"
          :page-size="12"
          @size-change="sizeChange"
          @current-change="pageChang">
        </el-pagination>
      </el-card>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancel"> </el-button>
        <el-button type="primary" @click="confirm"> </el-button>
      </span>
    </el-dialog>
  </div>

</template>

<script>
  import { getImages } from '../../api/images'

  export default {
    props: {
      picnum: {
        type: Number
      },
      fImages: {
        type: Array,
        default: function () {
          return []
        }
      }
    },
    data () {
      return {
        collect: false,
        imageList: [],
        total_count: 0,
        pageSize: 12,
        dialogVisible: false,
        curIndex: 0,
        curImage: null,
        images: this.fImages.length > 0 ? this.fImages : new Array(this.picnum),// 
        spanShow: [true, true, true]
      }
    },
    methods: {
      openDialog (index) {
        this.dialogVisible = true
        // 
        this.curIndex = index
        console.log('' + this.curIndex)
      },
      // 
      pageChang (val) {
        this.hGetImages(val)
      },
      hGetImages (val) {
        getImages({
          page: val || 1,
          per_page: this.pageSize,
          collect: this.collect
        }).then(res => {
          // 
          // 
          const list = res.data.data.results
          const newList = list.map((item) => {
            return {
              ...item,
              isShow: false
            }
          })
          this.imageList = newList
          this.total_count = res.data.data.total_count
        })
      },
      // 
      sizeChange (val) {
        this.pageSize = val
        this.hGetImages()
      },
      // 
      collectChange () {
        this.hGetImages()
      },
      cancel () {
      },

      isShow (item) {
        this.imageList.forEach(item => {
          item.isShow = false
        })
        item.isShow = true
        //  
        this.curImage = item
      },
      confirm () {
        //  
        //  images
        //  images[index] = curimage
        this.images[this.curIndex] = this.curImage.url
        this.dialogVisible = false
        if (this.images[this.curIndex]) {
          this.spanShow[this.curIndex] = false
        }
      },
      sendSelected () {
        return this.images
      }
    },
    mounted () {
      this.hGetImages()
      if (this.images[this.curIndex]) {
        this.spanShow[this.curIndex] = false
      }
    }
  }
</script>

