// 
// 
import request from '../utils/request'
// 
export const getImages = (params) => {
  // promise
  return request({
    method: 'GET',
    url: '/mp/v1_0/user/images',
    params
  })
}
// 
export const favorites = (id, collect) => {
  return request({
    method: 'PUT',
    url: `/mp/v1_0/user/images/${id}`,
    data: {
      collect
    }
  })
}

// 
export const delImages = (id) => {
  return request({
    method: 'DELETE',
    url: `/mp/v1_0/user/images/${id}`
  })
}
